import React, { useState, useEffect } from "react";
import classes from "./DenMap.module.css";
import coordinates from "../../Assets/Data/DenMap1.json";
import { useTranslation } from 'react-i18next';
import PlaceName from "../../Util/PlaceName";
import DenImg from "../../Assets/Images/DenMisc/inactive_den.png";
import ActiveDenImg from "../../Assets/Images/DenMisc/active1_den.png";
import ActiveDen2Img from "../../Assets/Images/DenMisc/active2_den.png";
import DenLocations from "../../Assets/Data/DenLocations_WA.json";
import NestMapping from "../../Assets/Data/JapEn.json";
import denNestMapping from "../../Assets/Data/Nests";

import {getCommonNest} from "../../Util/NestUtil";
import MiniEntry from "./MiniEntry";
import { Link } from "react-router-dom";
import Footer from "./Footer";

const areaClasses = [
    classes.BigMapLocation0, classes.BigMapLocation1, classes.BigMapLocation2, classes.BigMapLocation3, classes.BigMapLocation4, 
    classes.BigMapLocation5, classes.BigMapLocation6, classes.BigMapLocation7, classes.BigMapLocation8, classes.BigMapLocation9, 
    classes.BigMapLocation10, classes.BigMapLocation11, classes.BigMapLocation12, classes.BigMapLocation13, classes.BigMapLocation14, 
    classes.BigMapLocation15, classes.BigMapLocation16]

const areaDetailedClasses = [
    classes.DetailedMap0, classes.DetailedMap1, classes.DetailedMap2, classes.DetailedMap3, classes.DetailedMap4, 
    classes.DetailedMap5, classes.DetailedMap6, classes.DetailedMap7, classes.DetailedMap8, classes.DetailedMap9, 
    classes.DetailedMap10, classes.DetailedMap11, classes.DetailedMap12, classes.DetailedMap13, classes.DetailedMap14, 
    classes.DetailedMap15, classes.DetailedMap16]

const rowClasses = [classes.Sword, classes.Shield, classes.Both];

const DenMap = props => {
    // eslint-disable-next-line
    const [t, i18n] = useTranslation();
    const [selectedClass, setSelectedClass] = useState(0);
    const [activeDen, setActiveDen] = useState([0, 0]);
    const [activeNest, setActiveNest] = useState([]);
    const [activeNestIdx, setActiveNestIdx] = useState([]);

    useEffect(() => {
        activeNestHandler(selectedClass, activeDen[0], activeDen[1]);
    // eslint-disable-next-line
    }, [])

    const clickHandler = event => {
        const base = event.target.getBoundingClientRect();
        const [x,y] = [Math.floor(event.clientX - base.x), Math.floor(event.clientY - base.y)]
        for(var i = 0; i < 17; i++) {
            const coords = coordinates[i];
            for(var k = 0; k < coords.length; k++) {
                if(x === coords[k][0] && y === coords[k][1]) {
                    setSelectedClass(i);
                    setActiveDen([0, 0]);
                    activeNestHandler(i, 0, 0);
                }
            }
        }
    }

    const activeNestHandler = (area, idx, active) => {
        const den = NestMapping[area][idx];
        const nest = getCommonNest(denNestMapping[den][active]-1);
        setActiveNest(nest);
        setActiveNestIdx(denNestMapping[den][active])
    }

    const activateDen = idx => {
        if(activeDen[0] === idx) {
            if(activeDen[1] === 0) {
                setActiveDen([idx, 1]);
                activeNestHandler(selectedClass, idx, 1);
            } else {
                setActiveDen([idx, 0]);
                activeNestHandler(selectedClass, idx, 0);
            }
        } else {
            setActiveDen([idx, 0]);
            activeNestHandler(selectedClass, idx, 0);
        }
    }

    const detailClickHandler = event => {
        const base = event.currentTarget.getBoundingClientRect();
        const [x,y] = [Math.floor(event.clientX - base.x), Math.floor(event.clientY - base.y)]
        Object.values(DenLocations[selectedClass]).map((v, i) => {
            if(v[0] <= x && x < v[0] + 25 && v[1]<= y && y < v[1] + 25) {
                activateDen(i);
            }
            return null;
        });
    }

    return (
        <>
        <div className={classes.overallWrapper}>
        <h1>{t("wildareas.0")}: <PlaceName id={selectedClass}/></h1>
        <div className={classes.Container}>
            <div className={classes.BigMap}>
                <div className={areaClasses[selectedClass]} onClick={clickHandler} />
            </div>
            <div className={classes.DetailedMapCommon}><div className={classes.DetailMapMobile}>
            <div className={areaDetailedClasses[selectedClass]} onClick={detailClickHandler}>
                {
                    Object.keys(DenLocations[selectedClass]).map((v, i) => {
                        const val = DenLocations[selectedClass][v];
                        const im = activeDen[0] === i ? activeDen[1] === 0 ? ActiveDenImg : ActiveDen2Img : DenImg;
                        const left = val[0] - 20;
                        return <img src={im} key={i} alt="Den Map" width="15%" style={{"position": "absolute", "left": Math.floor(left*100/520) + "%", "bottom": Math.floor((390 - val[1] - 21)*100/390) + "%"}} />//onClick={event => activateDen(i)}
                    })
                }
            </div>
            </div>
            </div>
            <div className={classes.ContentTable}>
            <h2><Link to={/dens/ + activeNestIdx} className={classes.Unstyled}>{t("misc:NEST")} {activeNestIdx}</Link></h2>
            <div className={classes.Break} />
            <div className={classes.MonWrapper}>
                {
                    activeNest.map((entry, idx) => <MiniEntry key={idx} cssClass={rowClasses[entry.game]} data={entry}/>)
                }
            </div>
            </div>
        </div>
        </div>
        <Footer />
        </>
    );
}

export default DenMap;